import React from "react"
import { Global } from "@emotion/core"
import { Main, Styled, Container, css } from "theme-ui"
import "typeface-ibm-plex-sans"
import SEO from "./seo"
import Header from "./header"
import Footer from "./footer"
import CodeStyles from "../styles/code"
import SkipNavLink from "./skip-nav"
import Grid from '@material-ui/core/Grid';


type LayoutProps = { children: React.ReactNode; className?: string }

const Layout = ({ children, className }: LayoutProps) => (
  <Styled.root data-testid="theme-root">
    <Global
      styles={css({
        "*": {
          boxSizing: `inherit`,
        },
        body: {
          margin: 0,
          padding: 0,
          boxSizing: `border-box`,
          textRendering: `optimizeLegibility`,
        },
        "::selection": {
          backgroundColor: `primary`,
          color: `white`,
        },
        a: {
          transition: `all 0.3s ease-in-out`,
          color: `text`,
        },
      })}
    />
    <SEO />
    <SkipNavLink>Skip to content</SkipNavLink>

    <Grid container style={{maxWidth:"850px",margin:"auto"}}>
      
      <Grid item lg={12} md={12} sm={12} xs={12} style={{background:""}}>
      <Header />
      <Main id="skip-nav" css={css({ ...CodeStyles })} style={{marginTop:"100px"}} className={className}>
        {children}
      </Main>
      <Footer />
      </Grid>
      <Grid item lg={2}></Grid>
    </Grid>
    
    <Container sx={{width:"100px"}}>
      
    </Container>
  </Styled.root>
)

export default Layout
