/** @jsx jsx */
import { jsx, Styled } from "theme-ui"
import { Link } from "gatsby"
import useSiteMetadata from "../hooks/use-site-metadata"
import replaceSlashes from "../utils/replaceSlashes"

type NavigationProps = {
  nav: {
    title: string
    slug: string
  }[]
}

const Navigation = ({ nav }: NavigationProps) => {
  const { basePath } = useSiteMetadata()

  return (
    <nav sx={{ fontSize: [1, `18px`], ".active": { color: `heading`,background:"rgb(0,0,0,0.05)",border:"2px solid transparent"} ,".texthover:hover":{background:"rgb(0,0,0,0.05)",border:"2px solid transparent",color:`heading`,padding:"1rem",borderRadius:"4px",textDecoration:"none"}}}>
      {nav.map(item => (
        <Styled.a sx={{margin:"5px",border:"2px solid transparent",padding:"1rem",background:"",color:`secondary`,textDecoration:"none",transitionDuration:0,transition: ['background', 'color', 'font-size'],}} className="texthover" key={item.slug} as={Link} activeClassName="active" to={replaceSlashes(`/${basePath}/${item.slug}`)}>
          {item.title}
        </Styled.a>
      ))}
      
    </nav>
  )
}

export default Navigation
