/** @jsx jsx */
import React, { useState, useEffect } from 'react';
import { jsx, useColorMode, Styled } from "theme-ui"
import { Link } from "gatsby"
import { Box } from 'theme-ui'
import { Flex } from "@theme-ui/components"
import useSiteMetadata from "../hooks/use-site-metadata"
import ColorModeToggle from "./colormode-toggle"
import useNavigation from "../hooks/use-navigation"
import Navigation from "./navigation"
import replaceSlashes from "../utils/replaceSlashes"
import Grid from '@material-ui/core/Grid';
import MLink from '@material-ui/core/Link';



const Header = () => {
  const { siteTitle, externalLinks, basePath } = useSiteMetadata()
  const nav = useNavigation()
  const height="40px"
  const [colorMode, setColorMode] = useColorMode()
  const isDark = colorMode === `dark`
  const toggleColorMode = (e: any) => {
    e.preventDefault()
    setColorMode(isDark ? `light` : `dark`)
  }

  useEffect(()=>{
    const windowGlobal = typeof window !== 'undefined' && window
    windowGlobal.onscroll = function() {scrollFunction()};
    
    function scrollFunction() {
      if (document.body.scrollTop > 50 || document.documentElement.scrollTop > 50) {
        console.log("scrolling")
        document.getElementById("header").style.height = "60px";
        document.getElementById("title").style.marginTop = "10px"
        document.getElementById("navcomponents").style.marginTop = "10px"
        document.getElementById("colortoggle").style.marginTop = "10px";
        document.getElementById("title").style.fontSize = "32px"
      } else {
        console.log("decreased scrolling")
        document.getElementById("title").style.fontSize = "32px";
        document.getElementById("title").style.marginTop = "60px";
        document.getElementById("colortoggle").style.marginTop = "60px";
        document.getElementById("navcomponents").style.marginTop = "60px"
        document.getElementById("header").style.height = "100px";
      }
    };
  })

  const preventDefault = event => event.preventDefault();
  return (
    <header  sx={{ mb: [5, 6],transition:"0.2s",fontSize:"60px",background:"white",position:"fixed",maxWidth:"850px",margin:"auto",width:"100%"}} >
<Grid id="header" container style={{background:"white",transition:"0.2s",fontSize:"60px",position:"fixed",maxWidth:"850px",margin:"auto",width:"100%"}}>
<Grid item lg={3} md={3} sm={3} xs={3} >
  {/* <MLink to={replaceSlashes(`/${basePath}`)} onClick={preventDefault}>
    {siteTitle}
  </MLink> */}
<Link
          to={replaceSlashes(`/${basePath}`)}
          aria-label={`${siteTitle} - Back to home`}
          sx={{ color: `heading`, textDecoration: `none`,whiteSpace:"nowrap" }}
        >
          
          <h1 id="title" sx={{ my: 0, transition:"0.2s",fontWeight: `medium`, fontSize: [3, 4],marginTop:"70px" }}>{siteTitle}</h1>
        </Link>
</Grid>
<Grid item id="navcomponents" lg={8} md={8} sm={8} xs={8} style={{display:"flex",justifyContent:"flex-end",alignItems:"center",background:"",marginTop:"70px"}}>
<Navigation  nav={nav}  />
</Grid>
<Grid id="colortoggle" item lg={1} md={1} sm={1} xs={1} style={{display:"flex",justifyContent:"flex-end",alignItems:"center",marginTop:"70px"}}>
<ColorModeToggle isDark={isDark} toggle={toggleColorMode} />
</Grid>
</Grid>
     
      {/* <div
        sx={{
          boxSizing: `border-box`,
          display: `flex`,
          variant: `dividers.bottom`,
          alignItems: `center`,
          justifyContent: `space-between`,
          mt: 3,
          color: `secondary`,
          a: { color: `secondary`, ":hover": { color: `heading` } },
          flexFlow: `wrap`,
        }}
      >
        
        <div sx={{ padding:"1rem","a:not(:first-of-type)": { ml: 0 }, fontSize: [0, `18px`] ,".texthover:hover":{background:"rgb(0,0,0,0.05)",border:"2px solid transparent",padding:"1rem"}}}>
          {externalLinks.map(link => (
            <Styled.a className="texthover" sx={{transitionDuration:0,transition: ['background', 'color', 'font-size'],}} key={link.url} href={link.url} >
              {link.name}
            </Styled.a>
          ))}
        </div>
        <h1>You are buffallo</h1>
      </div> */}
    </header>
  )
}

export default Header
